.footer {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    z-index: 1;
    padding: 12px 0 9px;
    border-top: 1px solid rgba($color3, .2);
    background-color: #603eb8;
    @include xs {
        padding: 15px 0; }
    &__container {
        display: flex;
        justify-content: space-between;
        @include xs {
            flex-direction: column; } }
    &__text {
        flex: 0 0 285px;
        margin-right: 20px;
        font-size: 14px;
        color: #bfbbcb;
        @include sm {
            margin-right: 100px; }
        @include xs {
            margin: 0 0 15px;
            flex: 0 1 auto;
            width: 100%; } } }

