.contact-block {
    $parent: &;
    display: flex;
    align-items: center;
    line-height: 1;
    &_header {
        padding: 0 10px 0 20px;
        justify-content: space-between;
        @include sm {
            padding: 0; } }
    &_footer {
        flex: 0 1 766px;
        justify-content: space-between;
        @include xs {
            flex-direction: columns;
            flex: 0 1 auto;
            width: 100%; } }
    &__link {
        display: flex;
        align-items: inherit;
        font-size: 14px;
        color: $color3;
        transition: .3s ease-in-out;
        @include sm {
            font-size: 12px; }
        &:hover {
            color: $color5;
            svg {
                fill: $color5; } }
        #{$parent}_header & {
            &:nth-child(4) {
                display: none; } }

        #{$parent}_footer & {
            color: $white;
            &:hover {
                color: $color5; }
            &:not(:last-child) {
                @include xs {
                    margin-bottom: 15px; } } }
        svg {
            margin-right: 9px;
            fill: currentColor;
            font-size: 11px;
            transition: .3s ease-in-out; } }
    &__social {
        &-links {
            @include f('gp500');
            #{$parent}_header & {
                font-size: 12px;
                @include xs {
                    display: none; } }
            #{$parent}_footer & {
                font-size: 14px;
                @include sm {
                    display: none; }
                @include xs {
                    display: block;
                    margin-bottom: 15px; } } }
        &-icons {
            #{$parent}_header & {
                @include xs {
                    display: none; } }
            #{$parent}_footer & {} }

        &-link {
            display: inline-block;
            vertical-align: middle;
            text-shadow: 0 0 20px rgba(104, 193, 221, 0.2);
            color: $color5;
            line-height: 10px;
            transition: .3s ease-in-out;
            &:not(:last-child) {
                margin-right: 46px;
                @include sm {
                    margin-right: 6px; }
                @include xs {
                    margin-right: 15px; } }
            &:hover {
                transform: translateY(-5px); } }
        &-icon {
            display: inline-block;
            vertical-align: middle;
            transition: .3s ease-in-out;
            font-size: 12px;
            #{$parent}_footer & {
                &:not(:last-child) {
                    margin-right: 25px; } }
            &:hover {
                transform: translateY(-5px); }
            svg {
                fill: $color5; } } }
    &__btn {
        flex: 0 1 210px;
        max-width: 210px;
        color: #fff;
        font-size: 14px;
        @include sm {
            max-width: none; }
        @include xs {
            flex: 0 1 140px;
            padding: 12px 15px 10px; } } }
// end contact-block

// *** begin video-block *** //
.video-block {
    $parent: &;
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-bottom: 26px;
    height: 250px;
    border-radius: 4px;
    overflow: hidden;
    transition: .3s ease-in-out;
    box-shadow: 0 15px 40px rgba(18, 3, 58,0.15);
    &:hover {
        transform: translateY(-10px); }
    &.is-active {
        &::after {
            opacity: 9; } }
    &::after {
        @include coverdiv;
        pointer-events: none;
        background-color: $color2;
        transition: .3s ease-in-out;
        content: ''; }
    &__video {
        @include coverdiv;
        width: 100%;
        height: 100%;
        @include cover-video; }
    &__play-btn {
        @include vcenter;
        top: 51.5%;
        z-index: 1;
        @include trs;
        .is-active & {
            opacity: 0;
            pointer-events: none; }
        &:hover svg {
            transform: scale(1.2); }
        &-icon {
            width: 30px;
            height: 32px;
            fill: $white;
            @include trs; } }
    &__text {
        max-width: 240px;
        z-index: 1;
        color: #fff;
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        transition: .3s ease-in-out;
        .is-active & {
            pointer-events: none;
            opacity: 0;
            transform: translateY(100%); } }
    &__corner {
        position: absolute;
        width: 20px;
        height: 20px;
        z-index: 1;
        border-style: solid;
        border-color: $color3;
        transition: .3s ease-in-out;
        #{$parent}:hover & {
            opacity: 0; }

        .is-active & {
            pointer-events: none;
            opacity: 0; }
        &_top {
            top: 20px;
            left: 20px;
            border-left-width: 1px;
            border-top-width: 1px;
            #{$parent}:hover & {
                top: 0;
                left: 0; } }
        &_bot {
            bottom: 20px;
            right: 20px;
            border-bottom-width: 1px;
            border-right-width: 1px;
            #{$parent}:hover & {
                right: 0;
                bottom: 0; } }
        &__decor-triangle {
            pointer-events: none;
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 1;
            fill: rgba($white, .1);
            width: 286px;
            height: 274px;
            margin-top: -143px;
            margin-left: -137px;
            animation: rotating 30s linear infinite;
            transition: .3s ease-in-out;
            .is-active & {
                opacity: 0; } } } }
// end video block






